@import "../../../css/module/color";
@import "../../../css/module/fn";

.imgBlock {
	width: p2m(700);
	height: p2m(235);
	border-radius: p2m(8);
	margin: 0 auto;
	border: p2m(8) solid white;
	background:{
		//color: #eee;
		size: 100% 100%;
	}
	
	
	a {
		display: block;
		line-height: p2m(235);
		width: 100%;
		height: 100%;
		text-align: center;
		font-size: p2m(36);
		font-weight: 900;
		color:#fff;
		text-transform: uppercase;
	}
}

#ulBuyerList {
	width: p2m(700);
	height: p2m(235);
	border-radius: p2m(8);
	margin: 0 auto;
	
	li {
		height: p2m(128);
		line-height: p2m(128);
		font-size: p2m(28);
		padding: 0 p2m(50);
		@include shadow();
		background-color: #fff;
		
		a:active {
			color: $red;
		}
	}
}

#divBuyerThumbList {
	width: p2m(700);
	border-radius: p2m(8);
	margin: 0 auto;
	padding: p2m(50) p2m(25);
	background-color: #fff;
	@include shadow();
	
	> h1 {
		font-size: p2m(30);
		color: #000;
		margin-bottom: 1.5em;
		text-transform: capitalize;
	}
	
	> ul {
		display: flex;
		margin: 0 auto p2m(80);
		//width: p2m(650);
		
		> li {
			
			width: p2m(300);
			height: p2m(300);
			@include shadow();
			text-align: center;
			color: #888;
			font-size: p2m(24);
			
			&:nth-of-type(2n+1) {
				margin-right: p2m(30);
			}
			
			> img {
				display: block;
				margin: p2m(80) auto 1em;
				max-width: p2m(155);
				max-height: p2m(155);
			}
		}
	}
}